.auth-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100vh;
}

.auth-background {
  width: 100%;
  height: 100vh;
  background-image: url('../images/login_signup.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  z-index: -1;
  opacity: 1;
}

.right-overlay {
  width: 38.2%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.6);
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column; /* Arrange content vertically */
  align-items: center; /* Center content horizontally */
  justify-content: center; /* Center content vertically */
}

.form-logo {
  width: 400px; 
  height: auto;
  margin-bottom: 50px; /* Space between the logo and the form */
}

.form-uniLogo {
  width: 120px;  /* Adjusted width for better appearance */
  height: auto;
  position: absolute;  /* Positioning relative to its parent */
  top: 20px;  /* Distance from the top */
  right: 20px;  /* Distance from the right */
  margin-top: 0;  /* Removing unnecessary margin */
}


.auth-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1;
  max-width: 450px;
  width: 100%;
}

/* General input styling for all text fields */
.auth-form input[type="email"],
.auth-form input[type="password"],
.auth-form input[type="text"],
.auth-form input[type="date"] {
  width: 100%; /* Make all input fields full width */
  padding: 10px; /* Add padding for better usability */
  margin-bottom: 10px; /* Add spacing between fields */
  font-size: 16px; /* Uniform font size */
  border: 1px solid #ccc; /* Border for all fields */
  border-radius: 4px; /* Rounded corners for all fields */
  box-sizing: border-box; /* Include padding and border in width calculation */
}

/* Styling for labels */
.form-label {
  display: block; /* Ensure labels appear on their own line */
  margin-bottom: 5px; /* Add spacing below labels */
  font-size: 14px; /* Slightly smaller font for labels */
  color: #333; /* Darker text color for readability */
}

/* Inline fields for first name and last name */
.inline-fields {
  display: flex; /* Align first name and last name side by side */
  justify-content: space-between; /* Add spacing between fields */
  gap: 10px; /* Add a gap between fields */
  margin-bottom: 10px; /* Spacing below the group */
}

.inline-fields input[type="text"] {
  width: 100%; /* Make both fields fill available space */
}

/* Styling for buttons */
.button {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
}

.submit {
  background-color: green;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.submit:hover {
  background-color: darkgreen;
}

/* Styling for links */
.text-link a {
  color: blue;
  text-decoration: none;
}

.text-link a:hover {
  text-decoration: underline;
}

