* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.content-wrapper {
  display: flex;
  justify-content: center;
  flex-grow: 1;
}

h1 {
  font-size: 24px;
  text-align: center;
  margin: 20px;
}
