.toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: rgba(255, 255, 255, 0.2);
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 100;
}

.toolbar-logo h2 {
  margin: 0;
}

.toolbar-links a {
  margin: 0 15px;
  color: black;
  text-decoration: none;
  font-weight: bold;
}

.toolbar-links a:hover {
  text-decoration: underline;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../images/background.jpeg");
  background-size: cover;
  background-position: center;
  filter: brightness(60%);
  /* Darkens the background */
  opacity: 0.5;
  /* Fades the image */
  z-index: -1;
  /* Sends it behind content */
  justify-content: center;
}

.beginButton {
  background-color: #28a745;
  /* Green background */
  color: white;
  /* White text */
  font-size: 18px;
  /* Adjust font size */
  padding: 10px 20px;
  /* Padding for the button */
  border: none;
  /* Remove border */
  border-radius: 5px;
  /* Rounded corners */
  cursor: pointer;
  /* Pointer cursor on hover */
  transition: background-color 0.3s ease, transform 0.2s ease;
  /* Smooth hover effects */
  font-family: Arial, sans-serif;
  /* Font style */
  font-weight: bold;
  /* Bold text */
}

/* Hover Effect */
.beginButton:hover {
  background-color: #218838;
  /* Slightly darker green */
}

/* Active (Pressed) Effect */
.beginlButton:active {
  background-color: #1e7e34;
  /* Darker green */
  transform: scale(0.98);
  /* Slightly shrink the button */
}

/* Small Button Size */
.beginButton.small {
  font-size: 14px;
  padding: 5px 5px;
  max-height: 80%;
  max-width: 50%;
}

.factorContainer {
  flex-grow: 1;
  overflow-y: auto;
  overflow-x: auto;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  max-height: 100%;
}